*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .app {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background:linear-gradient(rgba(0, 131, 4, 0.3),rgba(0, 0, 0, 0.7)),
     url("https://images.pexels.com/photos/1108234/pexels-photo-1108234.jpeg?auto=compress&cs=tinysrgb&w=600") center/cover;
  }
  
  form{
      background-color: white;
      padding: 2rem 4rem;
      border-radius: 10px;
      /* box-shadow: 0px 0px 5px rgb(151, 150, 150); */
      box-shadow: 0px 0px 5px rgb(58, 142, 101) ;
  }
  
  
  h1{
      color: rgb(158, 162, 51);
      text-align: center;
      margin-bottom: 2rem;
  }
  
  button{
      width: 100%;
      padding: .8rem 1.5rem;
      border: none;
      background-color: rgb(25, 16, 34);
      color: white;
      border-radius: 5px;
      font-weight: bold;
      font-size: 1.1rem;
      cursor: pointer;
      margin-top: 1.5rem;
  }
  button:hover{
    background-color: rgb(149, 101, 197);
  }
  button:active{
    background-color: rgb(162, 99, 51);
  }